import React, { useState } from "react";
import { ArrowRepeat } from "react-bootstrap-icons";

export const MenuList = ({
  mainMenuList,
  menuList,
  handleShow,
  setIsLoading,
}) => {
  var timeNow = new Date();
  const [isTime, setIsTime] = useState(
    timeNow.getHours() + ":" + timeNow.getMinutes() + ":" + timeNow.getSeconds()
  );

  const recheckCredit = () => {
    // setIsLoading(true);
    setIsTime(
      timeNow.getHours() +
        ":" +
        timeNow.getMinutes() +
        ":" +
        timeNow.getSeconds()
    );
  };
  return (
    <div className="row section_menu_list pb-8">
      <div className="col-12 mb-4">
        <div
          // id="recheck_credit"
          onClick={recheckCredit}
          className="section_deposit_total"
          style={{ cursor: "pointer" }}
        >
          <img src="./images/wallet.png" style={{ maxWidth: "50px" }} alt="" />
          <h5>กระเป๋าเงิน</h5>
          <h2>
            <span id="customer-balance">0.00</span> บาท
          </h2>
        </div>
        <p className="text-center mt-1 txt_reload_credits">
          <ArrowRepeat /> ข้อมูล ณ เวลา{" "}
          <span className="credit_lastupdate">{isTime}</span>
        </p>
      </div>
      {mainMenuList.length > 0 &&
        mainMenuList.map((item, index) => {
          return (
            <>
              {item.position ? (
                <div key={index} className="col-4 section_menu mb-2">
                  <div className=" btn-gold animation-gold">
                    <a id="" href={item.path}>
                      <div
                        className="section_menu_set tour-btnplay"
                        style={{ minWidth: "85px", minHeight: "85px" }}
                      >
                        <img
                          src={item.image}
                          className="img_icon_set logo_BETFLIX_icon_set"
                          style={{
                            maxWidth: "50px",
                            top: "18px",
                            maxHeight: "46px",
                          }}
                          alt={item.name}
                        />
                      </div>
                    </a>
                  </div>
                  <h5
                    className="text-center"
                    style={{ position: "relative", top: "-20px" }}
                  >
                    เข้าเล่น
                  </h5>
                </div>
              ) : (
                <div className="col-4 section_menu mb-2" style={item.style[0]}>
                  <div
                    className="section_menu_set tour-deposit"
                    style={item.style[1]}
                  >
                    <a
                      href="javascript:;"
                      onClick={() => handleShow(item.modalType)}
                      className="menu_txt startCheck"
                    >
                      <img
                        src={item.image}
                        className="img_icon_set"
                        style={item.style[3]}
                        alt={item.name}
                      />
                    </a>
                  </div>
                  <h5 style={item.style[2]}>{item.name}</h5>
                </div>
              )}
            </>
          );
        })}

      {menuList.length > 0 &&
        menuList.map((item, index) => {
          return (
            <div key={index} className="col-3 section_menu mb-2">
              {item.modal ? (
                <a
                  href="javascript:;"
                  onClick={() => handleShow(item.path)}
                  className="menu_txt"
                >
                  <div className="section_menu_set">
                    <img
                      src={item.image}
                      className="img_icon_set"
                      style={item.style}
                      alt={item.name}
                    />
                  </div>
                  <h5 className="text-center">{item.name}</h5>
                </a>
              ) : (
                <a href={`./${item.path}`} className="menu_txt">
                  <div className="section_menu_set">
                    <img
                      src={item.image}
                      className="img_icon_set"
                      style={item.style}
                      alt={item.name}
                    />
                  </div>
                  <h5 className="text-center">{item.name}</h5>
                </a>
              )}
            </div>
          );
        })}
    </div>
  );
};
export default MenuList;
