import React from "react";
import Modal from "react-bootstrap/Modal";
import Deposite from "./Content/Deposite";
import Withdraw from "./Content/Withdraw";
import Annou from "./Content/Annou";
import ForgotPassword from "./Content/ForgotPassword";

export const Index = ({ show, handleClose, type }) => {
  const title = {
    deposit: "ฝากเงิน",
    withdraw: "ถอนเงิน",
    annou: "ประกาศ",
    forgotPassword: "เปลี่ยนรหัสผ่าน",
  };

  return (
    <>
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>
            <h5 className="text-highlight">{title[type]}</h5>
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {type === "deposit" && <Deposite />}
          {type === "withdraw" && <Withdraw />}
          {type === "annou" && <Annou />}
          {type === "forgotPassword" && <ForgotPassword />}
        </Modal.Body>
      </Modal>
    </>
  );
};
export default Index;
