import React, { useEffect, useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";
import swal from "sweetalert2";
import * as Icon from "react-bootstrap-icons";

const Register = () => {
  const [memberPhone, setMemberPhone] = useState();
  const [password1, setPassword1] = useState();
  const [password2, setPassword2] = useState();
  const [step1, setStep1] = useState(false);

  useEffect(() => {
    document.body.classList.add("bg-black");
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    if (memberPhone) {
      setStep1(true);
    } else {
      swal.fire("ข้อความ", "กรุณากรอก เบอร์โทรศัพท์ / รหัสผ่าน");
    }

    if (step1) {
      swal.fire("ข้อความ", "กรุณากรอกรหัสผ่านอย่างน้อย 6 ตัวอักษรค่ะ");
    }
  };

  return (
    <div className="container text-center" style={{ paddingTop: "4.2rem" }}>
      <div className="col-md-4 mx-auto">
        <div className="myform bg_myform_2">
          <div class="logo mb-3">
            <div className="col-md-12 text-center">
              <img
                src="/logo/logo.jpeg"
                alt="Bigwin"
                style={{ maxWidth: "100%", marginBottom: "15px" }}
              />
              <h2>สมัครสมาชิก</h2>
            </div>
          </div>

          <form noValidate onSubmit={handleSubmit}>
            <p className="mt-1 text-white small">
              กรุณากรอกเบอร์โทรศัพท์ของท่าน
            </p>
            <div className="form-group">
              {!step1 ? (
                <InputGroup className="mb-3">
                  <InputGroup.Text id="basic-addon1"><Icon.PersonFill /></InputGroup.Text>
                  <FormControl
                    placeholder="เบอร์โทรศัพท์"
                    aria-label="memberPhone"
                    aria-describedby="basic-addon1"
                    onChange={(e) => setMemberPhone(e.target.value)}
                  />
                </InputGroup>
              ) : (
                <>
                  <InputGroup className="mb-3">
                    <FormControl
                      placeholder="รหัสผ่าน"
                      type="password"
                      aria-label="password1"
                      aria-describedby="basic-addon1"
                      onChange={(e) => setPassword1(e.target.value)}
                    />
                  </InputGroup>
                  <InputGroup className="mb-3">
                    <FormControl
                      placeholder="ยืนยันรหัสผ่าน"
                      type="password"
                      aria-label="password2"
                      aria-describedby="basic-addon1"
                      onChange={(e) => setPassword2(e.target.value)}
                    />
                  </InputGroup>
                </>
              )}
            </div>
            <div className="mt-4">
              <button
                type="submit"
                className="next action-button btn btn-block mybtn icon btn_login tx-tfm"
              >
                ต่อไป{' '}<Icon.ArrowRight />
              </button>
            </div>
          </form>
          <div className="col-md-12">
            <div className="login-or">
              <hr className="hr-or" />
              <span className="span-or">หรือ</span>
            </div>
          </div>
          <div className="col-md-12 mb-3">
            <p className="text-white">
              มีบัญชีอยู่แล้ว{" "}
              <a
                style={{
                  color: "#fec33f",
                  textDecoration: "underlin",
                }}
                href="./login"
              >
                เข้าสู่ระบบ
              </a>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};
export default Register;
