import React, { useState } from "react";
import MenuList from "./MenuList";
import { defaultMenuList, defaultMainMenuList } from "./DefaultMenu";
import Modal from "../../components/Modal";
import { Power } from "react-bootstrap-icons";
import Spinner from "react-bootstrap/Spinner";

const Home = () => {
  const user = JSON.parse(localStorage.getItem("user"));
  const [show, setShow] = useState(false);
  const [modalType, setModalType] = useState("");
  const [isLoading, setIsLoading] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = (modalType) => {
    setShow(true);
    setModalType(modalType);
  };

  const handleLogout = () => {
    localStorage.removeItem("accessToken");
    localStorage.removeItem("user");
    window.location.href = "/";
  };

  return (
    <div id="card-game" className="text-white">
      <div
        className="container"
        style={{
          opacity: isLoading ? 0.4 : "unset",
          pointerEvents: isLoading ? "none" : "unset",
        }}
      >
        <div className="row pt-4">
          <div className="col-8 txt_head_set">
            <div id="tour-username" style={{ width: "150px" }}>
              <h4>ชื่อผู้ใช้</h4>
              <p>{user.fname}</p>
            </div>
          </div>
          <div
            className="col-4 txt_head_set text-right"
            style={{ paddingRight: "0" }}
          >
            <a onClick={handleLogout} style={{ cursor: "pointer" }}>
              <h4 className="txt_logout">
                <Power size={20} />
              </h4>
              <p className="txt_logout">ออกจากระบบ</p>
            </a>
          </div>
        </div>
        <MenuList
          mainMenuList={defaultMainMenuList}
          menuList={defaultMenuList}
          handleShow={handleShow}
          setIsLoading={setIsLoading}
        />
      </div>
      <Modal show={show} handleClose={handleClose} type={modalType} />
      <Spinner
        animation="border"
        variant="warning"
        style={{
          position: "absolute",
          display: isLoading ? "unset" : "none",
          top: "50%",
          left: "50%",
          color: "primary.main",
        }}
      />
    </div>
  );
};
export default Home;
