import React, { useState } from "react";
import axios from "axios";
// import swal from "sweetalert";
import "bootstrap/dist/css/bootstrap.min.css";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";
import Spinner from "react-bootstrap/Spinner";
import * as Icon from "react-bootstrap-icons";
import swal from "sweetalert2";
import Modal from "../../components/Modal";
import { loginAuth } from "../../utils/apiPath";

const Login = () => {
  const [username, setUserName] = useState();
  const [password, setPassword] = useState();
  const [isLoading, setIsLoading] = useState(false);
  const [show, setShow] = useState(false);
  const [showPassword, setShowPassword] = useState(true);
  const handleShow = () => setShow(true);
  const handleClose = () => setShow(false);
  const handleShowPassword = () => setShowPassword(!showPassword);

  const handleSubmit = async (e) => {
    e.preventDefault();
    const body = {
      username: username,
      password: password,
    };
    await axios({
      url: loginAuth,
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      data: body,
    })
      .then((res) => {
        const result = res.data;
        if ("accessToken" in result) {
          setIsLoading(true);
          localStorage.setItem("accessToken", result["accessToken"]);
          localStorage.setItem("user", JSON.stringify(result["user"]));
          window.location.href = "/";
        } else {
          swal.fire("ข้อความ", "กรุณากรอก เบอร์โทรศัพท์ / รหัสผ่าน");
        }
      })
      .catch((err) => {
        swal.fire("ข้อความ", "กรุณากรอก เบอร์โทรศัพท์ / รหัสผ่าน");
      });
  };

  return (
    <div className="container text-center" style={{ paddingTop: "4.2rem" }}>
      <div
        className="col-md-4 mx-auto"
        style={{
          opacity: isLoading ? 0.4 : "unset",
          pointerEvents: isLoading ? "none" : "unset",
        }}
      >
        <div className="myform bg_myform_1">
          <div className="logo mb-3">
            <div className="col-md-12 text-center">
              <img
                src="/logo/logo.jpeg"
                alt="Bigwin"
                style={{ maxWidth: "100%", marginBottom: "15px" }}
              />
            </div>
          </div>
          <form noValidate onSubmit={handleSubmit}>
            <div className="form-group">
              <InputGroup className="mb-3">
                <InputGroup.Text id="basic-addon1">
                  <Icon.PersonFill />
                </InputGroup.Text>
                <FormControl
                  id="email"
                  name="email"
                  placeholder="เบอร์โทรศัพท์"
                  aria-label="email"
                  aria-describedby="basic-addon1"
                  onChange={(e) => setUserName(e.target.value)}
                />
              </InputGroup>
            </div>
            <div className="form-group">
              <InputGroup className="mb-3">
                <InputGroup.Text id="basic-addon1">
                  <Icon.KeyFill />
                </InputGroup.Text>
                <FormControl
                  id="password"
                  name="password"
                  label="รหัสผ่าน"
                  type={showPassword ? "password" : "text"}
                  placeholder="รหัสผ่าน"
                  aria-label="password"
                  aria-describedby="basic-addon1"
                  onChange={(e) => setPassword(e.target.value)}
                />
                <InputGroup.Text id="basic-addon1" onClick={handleShowPassword}>
                  {showPassword ? <Icon.EyeSlash /> : <Icon.Eye />}
                </InputGroup.Text>
              </InputGroup>
            </div>
            <div className="form-group">
              <p className="text-right">
                <a href="#" onClick={handleShow} className="text-white">
                  ลืมรหัสผ่าน ?
                </a>
              </p>
            </div>
            <div className="col-md-12 text-center">
              <button type="submit" className="google btn mybtn">
                เข้าสู่ระบบ
              </button>
            </div>
            <div className="col-md-12 text-center">
              <div className="login-or">
                <hr className="hr-or" />
                <span className="span-or">หรือยังไม่มีบัญชี</span>
              </div>
            </div>
            <div className="col-md-12 mb-3">
              <p className="text-center">
                <a href="./register" className="btn btn-blu-grow">
                  สมัครสมาชิก
                </a>
              </p>
            </div>
            <div className="form-group">
              <p className="text-center">
                <a href="#" style={{ color: "#aaa" }}>
                  <Icon.Arrow90degLeft /> กลับไปหน้าแรก
                </a>
              </p>
            </div>
          </form>
        </div>
      </div>
      <Modal show={show} handleClose={handleClose} type={"forgotPassword"} />
      <Spinner
        animation="border"
        variant="warning"
        style={{
          position: "absolute",
          display: isLoading ? "unset" : "none",
          top: "50%",
          left: "50%",
          color: "primary.main",
        }}
      />
    </div>
  );
};
export default Login;
